*{margin:0;padding:0;font-family:'Red Hat Text', sans-serif;text-decoration:none;overflow:hidden}p{text-align:justify}.button-scroll{bottom:2em;cursor:pointer}.button-scroll button{display:flex;flex-direction:column;align-items:center;background:transparent;border:none}.button-scroll svg{height:2em;width:2em;padding:0.3em;transition:0.5s}.button-scroll svg:hover{background:#d72323;fill:#eee;border-radius:50%;animation:none}.down{animation:down 1.5s infinite}@keyframes down{0%{transform:translate(0, 0);opacity:0}50%{opacity:1}100%{transform:translate(0, 1em);opacity:0}}.up{animation:up 1.5s infinite}@keyframes up{0%{transform:translate(0, 0);opacity:0}50%{opacity:1}100%{transform:translate(0, -1em);opacity:0}}
